import axios from "axios";

console.log(process.env.NODE_ENV);
const api = axios.create({
  baseURL: `http://localhost:5000/api/v1/`,
});
const apiprod = axios.create({
  baseURL: `/api/v1/`,
});

const apiUse = () => {
  if (process.env.NODE_ENV === "development") {
    return api;
  } else {
    apiprod;
  }
};

export default apiUse();
